.container {
  padding: 23px 54px 142px 54px;
  background-color: rgba(239, 246, 243, 0.8);
  color: #61d741;
  overflow: hidden;

  &__inner {
    max-width: 1600px;
    margin: 0 auto;
    padding: 5px;
    display: flex;
    flex-direction: column;
  }
}

.nav {
  text-decoration: none;
  color: #000;
  font-size: 20px;
}

.errorbox {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  &__title {
    font-size: 30px;
  }

  &__text {
    font-size: 20px;
  }

  &__button {
    background-color: #c2fbd7;
    border-radius: 50px;
    border-width: 0;
    box-shadow: rgba(25, 25, 25, 0.04) 0 0 1px 0, rgba(0, 0, 0, 0.1) 0 3px 4px 0;
    color: #008000;
    cursor: pointer;
    display: inline-block;
    font-family: Arial, sans-serif;
    font-size: 1em;
    height: 50px;
    padding: 0 25px;
    transition: all 200ms;
    text-transform: uppercase;

    &:hover {
      background-color: #afe6c3;
      transform: scale(1.05);
      color: #000;
    }
  }
}
