.charmore {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  &__image {
    border: 5px solid #f2f2f7;
    width: 18.75rem;
    border-radius: 150px;
    margin-bottom: 10px;
  }

  &__name {
    font-weight: 400;
    font-size: 48px;
    line-height: 56px;
    text-align: center;
    margin: 0;
    padding: 0 0 48px 0;
  }

  &__infotitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #8e8e93;
    margin: 0;
    padding: 0 0 48px 0;
  }

  @media (max-width: 640px) {
    &__name {
      font-size: 32px;
      line-height: 38px;
    }

    &__image {
      width: 9.375rem;
    }

    &__infotitle {
      align-self: start;
    }

    &__infobox {
      width: 100%;
    }
  }
}
